.dsc-img__wrapper {
  position: relative;
  display: block;
  border-radius: 8px;
  border-width: 1px;
}

.dsc-img__wrapper--loading {
  background: #FFFFFF;
  border: 1px solid #CECECE;
}

.dsc-img__container {
  object-fit: cover;
  border-radius: 8px;
}

.dsc-img__placeholder {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
}
